import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import * as action from '../../../redux/actions/textFieldAction'
class TextFieldFormEdit extends Component {

  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditTagChange(e.target.value);
  };
  handleChangeLabel = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditLabelChange(e.target.value);
  };
  handleChangeValue = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditValueChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditTopChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditWidthChange(e.target.value);
  };
  handleChangeColor = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditColorChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditFontSizeChange(e.target.value);
  };
  handleChangeRows = (e) => {
    e.preventDefault();
    this.props.actionTextFieldEditRowsChange(e.target.value);
  };
 
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionUpdateTextField();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37) {//Trai
      this.props.actionEditMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38) {//Len
      this.props.actionEditMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39) {//Phai
      this.props.actionEditMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40) {//Xuong
      this.props.actionEditMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key={this.props.id} onKeyDown={this.handleKeyPress} onKeyUp={this.handleKeyUp}>
          <h3 className="text-center text-info">CHANGE TEXT FIELD PARAMETER</h3>
          <button type="button" onClick=
            {this.props.actionUpdateTextField}
            className="btn btn-primary btn-block">Update</button>

          <div className="dropdown-divider"></div>

          <button type="button" onClick=
            {this.props.actionDeleteTextField}
            className="btn btn-primary btn-block">DELETE</button>
          <div className="form-group">
            <small id="id" className="form-text text-muted">ID</small>
            <input value={this.props.id} type="text" className="form-control" name="id" id="id" aria-describedby="helpIdTitle" placeholder="ID: " />

            <small className="form-text text-muted">Tag</small>
            <input value={this.props.tag} onChange={this.handleChangeTag} type="text" className="form-control" name="text" id="tag" aria-describedby="helpIdTitle" placeholder="Tag: " />

            <small className="form-text text-muted">Label</small>
            <input value={this.props.label} onChange={this.handleChangeLabel} type="text" className="form-control" name="text" id="label" aria-describedby="helpIdTitle" placeholder="Label: " />

            <small className="form-text text-muted">Value</small>
            <input value={this.props.value} onChange={this.handleChangeValue} type="text" className="form-control" name="text" id="value" aria-describedby="helpIdTitle" placeholder="Value: " />

            <small id="left" className="form-text text-muted">Left</small>
            <input value={this.props.left} onChange={this.handleChangeLeft} type="text" className="form-control" name="left" id="left" aria-describedby="helpIdTitle" placeholder="Left: " />

            <small id="top" className="form-text text-muted">Top</small>
            <input value={this.props.top} onChange={this.handleChangeTop} type="text" className="form-control" name="top" id="top" aria-describedby="helpIdTitle" placeholder="Top: " />

            <small id="width" className="form-text text-muted">Width</small>
            <input value={this.props.width} onChange={this.handleChangeWidth} type="text" className="form-control" name="width" id="width" aria-describedby="helpIdTitle" placeholder="Width: " />

            <small id="top" className="form-text text-muted">Color</small>
            <input type="color" style={{width:'100%'}} id="color" onChange={this.handleChangeColor} value={this.props.color} />
            <input value={this.props.color} onChange={this.handleChangeColor} type="text" className="form-control" name="color" id="color" aria-describedby="helpIdTitle" placeholder="Color: " />

            <small id="top" className="form-text text-muted">FontSize</small>
            <input value={this.props.fontSize} onChange={this.handleChangeFontSize} type="text" className="form-control" name="fontSize" id="fontSize" aria-describedby="helpIdTitle" placeholder="FontSize: " />

            <small id="rows" className="form-text text-muted">Rows</small>
            <input value={this.props.rows} onChange={this.handleChangeRows} type="text" className="form-control" name="fontSize" id="rows" aria-describedby="helpIdTitle" placeholder="Rows: " />

          </div>

        </form>
      </Col>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    id: state.textField.editTextField.id,
    tag: state.textField.editTextField.tag,
    label: state.textField.editTextField.label,
    value: state.textField.editTextField.value,
    left: state.textField.editTextField.left,
    top: state.textField.editTextField.top,
    width: state.textField.editTextField.width,
    color: state.textField.editTextField.color,
    fontSize: state.textField.editTextField.fontSize,
    rows: state.textField.editTextField.rows,
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    actionUpdateLabel: (editItem) => {
      dispatch(action.actionUpdateTextField(editItem))
    },
    actionDeleteLabel: (id) => {
      dispatch(action.actionDeleteTextField(id))
    },
    actionTextFieldEditTagChange: (i) => {
      dispatch(action.actionTextFieldEditTagChange(i))
    },
    actionTextFieldEditLabelChange: (i) => {
      dispatch(action.actionTextFieldEditLabelChange(i))
    },
    actionTextFieldEditValueChange: (i) => {
      dispatch(action.actionTextFieldEditValueChange(i))
    },
    actionTextFieldEditLeftChange: (i) => {
      dispatch(action.actionTextFieldEditLeftChange(i))
    },
    actionTextFieldEditTopChange: (i) => {
      dispatch(action.actionTextFieldEditTopChange(i))
    },
    actionTextFieldEditWidthChange: (i) => {
      dispatch(action.actionTextFieldEditWidthChange(i))
    },
    actionTextFieldEditColorChange: (i) => {
      dispatch(action.actionTextFieldEditColorChange(i))
    },
    actionTextFieldEditFontSizeChange: (i) => {
      dispatch(action.actionTextFieldEditFontSizeChange(i))
    },
    actionTextFieldEditRowsChange: (i) => {
      dispatch(action.actionTextFieldEditRowsChange(i))
    },

    actionEditMoveLeftDecrease: (i) => {
      dispatch(action.actionEditMoveLeftDecrease(i))
    },
    actionEditMoveLeftIncrease: (i) => {
      dispatch(action.actionEditMoveLeftIncrease(i))
    },
    actionEditMoveTopDecrease: (i) => {
      dispatch(action.actionEditMoveTopDecrease(i))
    },
    actionEditMoveTopIncrease: (i) => {
      dispatch(action.actionEditMoveTopIncrease(i))
    },
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(TextFieldFormEdit);